class Hero extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
    .content {
        .hero {
          height: 30em;
          display: flex;
          flex-direction: column;
          place-content: center;
          gap: 1em;
          text-align: center;
          color: var(--white);
          position: relative;

          .hero_title {
            font-weight: 700;
            text-shadow: black 0.1rem 0 0.75rem;
            color: var(--white);
            z-index: 50;
          }

          .hero_tagline {
            font-weight: 600;
            font-style: italic;
            text-shadow: black 0.1rem 0 0.75rem;
            z-index: 50;
          }

          .hero_image {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            filter: brightness(70%);
            z-index: 0;

          }
        }
        }
      }
    `;
  }

  render() {
    this.updateStyle();

    this.innerHTML += `
      ${this._style.outerHTML}
      <div class="hero">
        <picture>
          <source media="(max-width: 600px)" srcset="./images/heros/hero-image-small.webp" type="image/webp">
          <source media="(max-width: 600px)" srcset="./images/heros/hero-image-small.jpg" type="image/jpeg">
          <source srcset="./images/heros/hero-image-large.webp" type="image/webp">
          <source srcset="./images/heros/hero-image-large.jpg" type="image/jpeg">
          <img src="./images/heros/hero-image-large.jpg" alt="Gambar Hero" class="hero_image lazyload" loading="lazy">
        </picture>
        <h1 tabindex="0" class="hero_title">Cari makanan favoritmu di Karest</h1>
        <p tabindex="0" class="hero_tagline"
          >jelajahi kuliner di seluruh indonesia, rasakan cita rasanya sebelum
          pergi kesana</p
        >
      </div>
    `;
  }
}

customElements.define('main-hero', Hero);
