class FootBar extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      .footer {
        background-color: var(--primaryColor);
        color: var(--white);
        display: flex;
        flex-direction: column;
        place-items: center;
        justify-content: center;

        padding: 0.5em 0;
        max-width: 100%;
      }
    `;
  }

  render() {
    this.updateStyle();

    this.innerHTML += `
      ${this._style.outerHTML}
      <footer class="footer">
        <p tabindex="0">Karest Apps | katalog restoran</p>
        <p tabindex="0">copyright &copy; 2024</p>
      </footer> 
    `;
  }
}

customElements.define('foot-bar', FootBar);
