class AppBarLogo extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      .header_logo {
        img {
          height: 3em;
        }
      }
    `;
  }

  render() {
    this.updateStyle();

    this.innerHTML += `
      ${this._style.outerHTML}
        <div class="header_logo">
          <picture>
            <source type="image/webp" data-srcset="./images/karest.webp">
            <source type="image/jpeg" data-srcset="./images/karest.jpg">
            <img tabindex="0" data-src="./images/karest.jpg" alt="logo-karest" class="lazyload" loading="lazy">
          </picture>
        </div>
    `;
  }
}

customElements.define('app-bar-logo', AppBarLogo);
