class AppBar extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      .header {
        padding: 0.3em;
        display: flex;
        flex-direction: row;
        place-items: center;
        justify-content: space-between;
        background-color: var(--white);
        box-shadow: 0.1rem 0.1rem 0.1rem var(--primaryColor);
        position: sticky;
        top: -1px;
        z-index: 110;
      }
    `;
  }

  render() {
    this.updateStyle();

    this.innerHTML += `
      ${this._style.outerHTML}

      <header class="header">
        <app-bar-logo></app-bar-logo>
        <app-bar-menu></app-bar-menu>
        <app-bar-nav id="drawer"  class="nav"></app-bar-nav>
      </header>
    `;
  }
}

customElements.define('app-bar', AppBar);
