class AppBarNav extends HTMLElement {
  constructor() {
    super();
    this._style = document.createElement('style');
  }

  connectedCallback() {
    this.render();
  }

  updateStyle() {
    this._style.textContent = `
      .nav {
        display: none;
        .nav_list {
          .nav_item {
            list-style: none;

            a {
              font-size: 1.2rem;
              padding: 0.6em;

              text-decoration: none;
              color: var(--black);

              &:hover,
              &:active {
                color: var(--primaryColor);
              }
            }
          }
        }
      }
    `;
  }

  render() {
    this.updateStyle();

    this.innerHTML += `
      ${this._style.outerHTML}

      <nav>
        <ul class="nav_list">
          <li class="nav_item"><a href="#/list-restaurants">Home</a></li>
          <li class="nav_item"><a href="#/favorite">Favorite</a></li>
          <li class="nav_item"><a href="https://github.com/NMSID" target="_blank">About Us</a></li>
        </ul>
      </nav>
    `;
  }
}

customElements.define('app-bar-nav', AppBarNav);
